}

.profile-modal {
    background: #22212100 !important;
}

.caleandar-modal .modal-dialog {
    max-width: 384px !important;
}
button:focus{
    box-shadow: none !important;
}
.join {
    padding: 4px 20px;
    font-size: 14px;
    font-weight: 500;
    border: 1px solid rgb(98, 100, 167) !important;
    border-radius: 2px;
    background-color: rgb(98, 100, 167) !important;
    color: white !important;
    margin-bottom: 14px;
}

.link {
    display: inline-flex;
    overflow: hidden;
    width: 183px;
    /* white-space: nowrap; */
}

.fc-scrollgrid {
    background: white !important;
}

.calendar-wrapper {
    background: #fcfbf9 !important;
}

.fc-header-toolbar {
    padding: 0px 15px !important;
    margin: 0px !important;
}

.fc {
    /* color: 100% !important;   */
}

.calendar-wrapper {
    /* display: flex;
    /* min-height: 100%; */
    /* font-family: Arial, Helvetica Neue, Helvetica, sans-serif;
    font-size: 14px; */
}

.calendar-wrapper-content {
    /* flex-grow: 1;
    padding: 3em; */
}

.fc-event, .fc-event:hover {
    /* color: #fff !important; */
}

.fc-common, .fc-event {
    padding-top: 2px !important;
    padding-left: 3px !important;
    border-top: none !important;
    /* height: 36px !important; */
    color: #46465a !important;
    border-right: none !important;
    border-bottom: none !important;
    text-transform: capitalize !important;
}

.fc-blue, .fc-event {
    background-color: #dfddf3 !important;
    border-left: 4px solid #6364a7 !important;
    cursor: pointer !important;
}

.fc-red {
    background-color: #faeceb !important;
    border-left: 4px solid #cc4a32 !important;
}

.fc-prev-button, .fc-next-button {
    background: #fcfbf9 !important;
    color: #212b56 !important;
    border: #fcfbf9 !important;
}

.fc-today-button {
    background: #fcfbf9 !important;
    font-weight: 500 !important;
    color: black !important;
    border: none !important;
    text-transform: capitalize;
    cursor: pointer;
}

button.fc-dayGridMonth-button, button.fc-timeGridWeek-button, button.fc-timeGridDay-button {
    color: black !important;
    background: #fcfbf9 !important;
    border: none !important;
}

button.fc-dayGridMonth-button, button.fc-timeGridWeek-button, button.fc-timeGridDay-button:focus {
    border: none !important;
    outline: none !important;
    box-shadow: unset !important;
}

.fc-toolbar.fc-header-toolbar {
    height: 35px !important;
}

.fc-button {
    text-transform: capitalize!important;
}

.fc .fc-toolbar-title {
    font-size: 16px !important;
    font-weight: 600 !important;
}

.fc-timeGridDay-button, .fc-timeGridWeek-button {
    margin-left: 1px !important;
}

.img-wrap-calendar {
    width: 30px !important;
    height: 30px !important;
}

td.fc-daygrid-day.fc-day * {
    white-space: break-spaces !important;
    overflow-wrap: anywhere !important;
}

.fc-media-screen .fc-timegrid-event {
    position: unset !important;
    white-space: break-spaces !important;
    overflow-wrap: anywhere !important;
}
.fc-direction-ltr .fc-timegrid-slot-label-frame {
    background: #f1f0ee !important ;
}
td.fc-timegrid-slot.fc-timegrid-slot-label.fc-timegrid-slot-minor {
    background: #f1f0ee !important;
}
.fc-col-header , .fc-daygrid-body , .fc-scrollgrid-sync-table{
    width: 100% !important;
}
.fc .fc-button-primary:focus{
    box-shadow: none !important;
}